import { reactive, readonly } from 'vue';

/**
 * @description AI聊天状态
 * @type {Object}
 * @property {boolean} isTesting - 是否正在测试中
 * @property {string|null} testResult - 测试结果
 * @property {boolean} isLoading - 是否正在加载
 * @property {Object|null} error - 错误信息
 */
const state = reactive({
  isTesting: false,
  testResult: null,
  isLoading: false,
  error: null
});

/**
 * @description 导出只读状态，防止在组件中直接修改状态
 */
export const useAIChatState = () => readonly(state);

/**
 * @description 导出内部状态，供actions使用
 */
export const internalState = state; 